#root {
  background-color: #e0e1dd;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 9px;
}
.button {
  height: 36px;
  border-radius: 9px;
  margin-left: 1px;
  background-color: white;
}

.app_header {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
}
.input {
  height: 30px;
  border-radius: 9px;
  background-color: white;
}
.forecast {
  display: flex;
  flex-direction: column;
  background-color: #778da9;
  margin-left: 3%;
  margin-right: 3%;
  border-radius: 9px;
  align-items: center;
  text-align: center;
  text-wrap: balance;
}
.todayweather {
  background-color: #cccccc;
  width: 94%;
  display: flex;
  flex-direction: column;
  border-radius: 9px;
  align-items: center;
  margin-left: 3%;
  margin-right: 3%;
}
